
.bg {
  position: fixed;
  display: block;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  background-image: url('https://myprojectsfromportfolio.s3.ca-central-1.amazonaws.com/vancouverbg.png')
}
#intro {
  font-family: 'Raleway', sans-serif;
  text-shadow: 2px 2px #000;
  height: 100vh;
  color: #FAFAFA;
}
.form {
  background-color: #2A2A2A !important;
  box-shadow: none;
  border: 0;
  color: white !important;
}

.form-control {
  border: 0 !important;
}
.navbar {
  z-index: 1;
}
#about, #project, #contact {
  color: white;
}
